
import { defineComponent } from 'vue'

export const THEME_SWITCH_EVENT_NAME = "theme-switch";

export default defineComponent({
    name: 'Navbar',

    emits: [
        THEME_SWITCH_EVENT_NAME,
    ],

    props: {
        checked: Boolean,
    },

    methods: {
        spreadEvent(status: boolean) {
            this.$emit(THEME_SWITCH_EVENT_NAME, status)
        }
    }
})
