
import { defineComponent } from 'vue'

export default defineComponent({
    name: 'Options',

    props: {
        primary: String,
        secondary: String,
        primaryHref: {
            type: String,
            default: "#"
        },
        secondaryHref: {
            type: String,
            default: "#"
        }
    },

    computed: {
        hasBothOptions() {
            return !!this.primary && !!this.secondary
        }
    }
})
